import { Component, OnInit, Input,OnChanges,SimpleChanges } from '@angular/core';
import { CallApiService, ApiCmd,ApiReloadService } from "../../../../shared/services";
declare var google: any;

@Component({
	selector: 'app-project-view-workers',
	templateUrl: './project-view-workers.component.html',
	styleUrls: ['./project-view-workers.component.scss']
})
export class ProjectViewWorkersComponent implements OnInit,OnChanges {
	@Input()
	project:any[];
	@Input()
	polygonArrays:any[];
	//polygon arrays already initialized with setPolygon function
	initializedPolygonArrays:any[]=[];
	edit:boolean=false;//to add/remove workers from project

	rowsOnPage:number = 10;
	sortBy:string = "w_id";
	sortOrder:string = "asc";
	filterQuery:string = "";
	defaultNull:string = "-";
	constructor(private callApi:CallApiService,
				private reloadService:ApiReloadService) { 
	}

	ngOnChanges(simpleChanges:SimpleChanges){
		if(simpleChanges['project']){
			console.log("project-view-workers input changes");
		}
	}

	ngOnInit() {
		console.log(this.project);
		console.log(this.polygonArrays);
		// this.makePolygon();
	}

	makePolygon(){
		// this.polygonArrays.forEach((poly)=>{
  //         this.setPolygon(poly.location,poly.zoneName);
  //       });
  //       console.log(this.initializedPolygonArrays);
        this.checkZone();

	}

	setPolygon(polygon:any[],name:string){
		let area = new google.maps.Polygon({
          paths: polygon
        });
        this.initializedPolygonArrays.push({area:area,name:name});
	}

	checkZone(){
		let pad = 10e15;
		this.project['p_workers'].forEach((worker)=>{
			this.project['zone'].forEach((zone)=>{

				let x = worker.latitude*pad, y = worker.longitude*pad;
				let poly = zone.zone_latlng[0];

				let inside = false;
				for(var i=0, j=poly.length-1; i<poly.length; j=i++){
					let xi = poly[i].lat*pad, yi = poly[i].lng*pad;
					let xj = poly[j].lat*pad, yj = poly[j].lng*pad;

					let intersect = ((yi>y)!=(yj>y)) && (x < (xj-xi) * (y-yi) / (yj - yi) + xi);
					if(intersect) inside = !inside;
				}
				if(inside){
					worker.zone = zone.zone_id;
				}

			});
			// let coordinate={lat: +worker.latitude,lng: +worker.longitude } as object;
			// let marker = new google.maps.Marker({
			//     position: coordinate
			//  });
			//check if inside polygon
			// this.initializedPolygonArrays.forEach((polygon)=>{
			// 	if(google.maps.geometry.poly.containsLocation(marker.getPosition(), polygon.area)){
			// 		worker.zone=polygon.name;
			// 	}	
			// });
		});
	}

	addWorkerToProject(projectId,employeeId){
		let add:any={
			newp_id:projectId,
			w_id:employeeId,
			p_id:''
		}
		add=JSON.stringify(add);
		console.log(add);
		this.callApi.postData(ApiCmd.addOrRemoveWorker,add)
		.subscribe((response)=>{
			console.log(response);
			this.reloadService.setReload();
		})
	}

	removeWorkerFromProject(projectId,employeeId){
		//{"type":"add/remove","p_id":"xxx","w_id":"xxx"}
		let remove:any={
			p_id:projectId,
			w_id:employeeId,
			newp_id:''
		};
		remove=JSON.stringify(remove);
		console.log(remove);
		this.callApi.postData(ApiCmd.addOrRemoveWorker,remove)
		.subscribe((response)=>{
			console.log(response);
			this.reloadService.setReload();
		})

	}



}



