import { Component, OnInit } from '@angular/core';
import { CallApiService, ApiCmd } from "../../shared/services";

//external library
import { DataTableModule } from 'angular2-datatable';

//testing
import { Data } from '../../shared';

//environment
import { environment } from '../../../environments/environment';
// import { setTimeout } from 'timers';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
	areaChartData:any;
	duties:any[];
	alerts:any[];
	sos:any[];
	unsafeWorkers:any[];
	areaChart:Chart;
	dutyChart:any[];
	//For dataTable initial sort order
	asc:string="asc";
	
	constructor(private callApi:CallApiService) { 
		// this.duties=Data.employees;
		// this.alerts=Data.alerts;
		// this.sos=Data.sosAlerts;
		// this.unsafeWorkers=Data.unsafeWorkers;
	}
	
	ngOnInit() {
		// setInterval(()=>{console.log("1 second passed")},1000);
		this.getSosList(ApiCmd.sosList); 
		this.getDutyList(ApiCmd.dutyList)
		this.getAlertList(ApiCmd.alertList);
		this.getDutyChart(ApiCmd.dutyChart);
		// this.getWorkerUnsafeData(ApiCmd.injuryGraph);
	}

	// get sos list
	getSosList(url:string){
	  this.callApi.getData(url)
	    .subscribe(data=>{
	      this.sos=data;
	      })
	}

	// get alert list
	getAlertList(url:string){
	  this.callApi.getData(url)
	    .subscribe(data=>{
	      this.alerts=data;
	      })
	}

	// get duty list
	getDutyList(url:string){
	  this.callApi.getData(url)
	    .subscribe(data=>{
	      this.duties=data;
	      })
	}

	getWorkerUnsafeData(url:string):void{
		this.callApi.getData(url)
			.subscribe(data=>{
				this.unsafeWorkers=data[0].data;
				this.getLineChart();
			})
	}

	getLineChart():void{
		// let areaData=[] as any;
		// areaData.push(['year','Unsafe workers']);
		// for (let worker of this.unsafeWorkers) {
		// 	areaData.push([worker.year,+worker.number]);
		// }
		// this.areaChartData =  {
		//   chartType: 'AreaChart',
		//   dataTable: areaData,
		//   options: {
		//   		'legend':'top',
		//   		'height':600,
		//   		'colors':['red','rgb(192,0,0)']
		//   },
		// };
		let arr=[];
		let series=[];
		this.unsafeWorkers.forEach((year)=>{
			series.push({name:year.year, value:+year.number});
		})
		arr.push({name:"worker",series:series});
		console.log(arr);
		this.areaChart= new Chart(arr);

	}

	getDutyChart(url:string):void{
		this.callApi.getData(url).subscribe(
			data=>{
				this.dutyChart=data;
			});
	}

}

export class Chart {
	data: any[];

	view: any[];

	// options
	showXAxis = true;
	showYAxis = true;
	gradient = false;
	showXAxisLabel = true;
	xAxisLabel = 'Year';
	showYAxisLabel = true;
	yAxisLabel = 'Number of Injuries';

	colorScheme = {
		domain: ['#5AA454', '#A10A28', '#C7B42C', '#AAAAAA']
	};

	// line, area
	autoScale = false;

	constructor(data: any[]) {
		this.data = data;
	}

	onSelect(event) {
		console.log(event);
	}

}


