import { Component, OnInit, Input, EventEmitter, Output ,ViewEncapsulation,ViewChild} from '@angular/core';
import { BsDatepickerConfig } from 'ngx-bootstrap/datepicker';
import { FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CallApiService, ApiCmd } from "../../../../shared/services";

@Component({
	selector: 'app-project-detail',
	templateUrl: './project-detail.component.html',
	encapsulation:ViewEncapsulation.None,
	styleUrls: ['./project-detail.component.scss']
})
export class ProjectDetailComponent implements OnInit {

	@ViewChild('fileInput')
	fileInput: any;
	constructor(private callApi:CallApiService) { 
		this.getManagerList(ApiCmd.managerList);
	}

	@Input() 
	group: FormGroup;
	photo:any;
	@Output()
	file = new EventEmitter<string>();
	preview;
	items:Array<any> = [];

	//datepicker config
	bsConfig: Partial<BsDatepickerConfig> = { containerClass: "theme-default", /*locale: "en-gb",*/ showWeekNumbers: false, dateInputFormat: 'YYYY/MM/DD ' };
	bsValue: Date;
	//dropdown list
	manager: any[];
	ngOnInit() {
	}

	fileUpload(event){
		//preview
		if (event.target.files && event.target.files[0]&&this.isImage(event)) {
		  var reader = new FileReader();
		  reader.readAsDataURL(event.target.files[0]);

		  reader.onload = (event:any) => {
				this.preview = event.target.result;
		  }
			this.photo=event.srcElement.files;
			this.file.emit(this.photo);

		}else{
			alert("Please upload image only");
			this.fileInput.nativeElement.value = "";
			this.preview=null;
		}
	}
	isImage(event):boolean{
		let file =event.target.files[0];
		let fileType = file["type"];
		fileType=fileType.split("/");
		if(fileType[0]=="image"){
			return true;
		}

		return false;
	}
	

	getManagerList(url){
		this.callApi.getData(url)
		.subscribe(data=>{
			this.manager=data;
			// console.log(manager);
			this.assignToDropdown();
		})
	}

	//test
 		
 		//assign to items variable accroding to ng2-select
		assignToDropdown(): void {
		  this.manager.forEach((admin) => {
		    this.items.push({
		      id: admin.u_id,
		      text:admin.u_name
		    });
		  });
		}

	  private value:any = {};

	  public selected(value:any):void {
	  	this.group.controls["projectAdmin"].patchValue(value.id);
	    console.log('Selected value is: ', value);
	  }

}



