import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CallApiService, ApiCmd } from "../../../../shared/services";

@Component({
	selector: 'app-send-command',
	templateUrl: './send-command.component.html',
	styleUrls: ['./send-command.component.scss']
})
export class SendCommandComponent implements OnInit {

	@Input() workerID;
	@Output() sent=new EventEmitter<object>();
	constructor(private CallApi:CallApiService) { }

	ngOnInit() {
	}

	sendCommand(event){
		console.log(event);
		let indicationMessage:Array<string>=new Array<string>();
		let data = {
			name : event
		};
		if(event=="heartrate"){
			indicationMessage[0]="Success";
			indicationMessage[1]="Heart rate reading command have been sent, please wait until the heart rate data is updated (~ 1 min)";
		}else if(event=="shutdown"){
			indicationMessage[0]="Success";
			indicationMessage[1]="Shut down command have been sent";
		}
		this.CallApi.sendCommand(data, this.workerID).subscribe((response)=>{
			this.sent.emit({indicationMessage:indicationMessage,commandSend:true});
			console.log(response);
		});
	}

}



