import { Component, OnInit } from '@angular/core';
import { LeafletModule } from '@asymmetrik/ngx-leaflet';
import { ActivatedRoute } from '@angular/router';
import { CallApiService,ApiCmd} from '../../../../shared/services';
import { DatePipe } from '@angular/common';
import * as L from 'leaflet';
import * as L_draw from 'leaflet-draw';
import { BsDatepickerConfig  } from 'ngx-bootstrap/datepicker';
import { FormControl, FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-employee-history-map',
  templateUrl: './employee-history-map.component.html',
  styleUrls: ['./employee-history-map.component.scss']
})
export class EmployeeHistoryMapComponent implements OnInit {
  map: L.map;
  historyLayers = new L.FeatureGroup;
  workerHistTrail:Array<any>=[];
  dataLoaded:boolean=false;
  initZoom = 8;

  //datepicker config
	bsConfig: Partial<BsDatepickerConfig> = { containerClass: "theme-default", showWeekNumbers: false, dateInputFormat: 'YYYY/MM/DD' };
  minDate:Date = new Date();
  maxDate:Date = new Date();
  Form:FormGroup;

  constructor(private _route: ActivatedRoute,
              private callApi: CallApiService,
              private datePipe: DatePipe,
              private _bsDatepickerConfig:BsDatepickerConfig) {
                this._bsDatepickerConfig.dateInputFormat = 'YYYY/MM/DD';
                this.minDate.setDate(this.minDate.getDate()-365);
                this.maxDate.setDate(this.maxDate.getDate()+1);
              }

  ngOnInit() {
    let id=this._route.snapshot.paramMap.get('id');
    this.getWorkerHistLoc(`${ApiCmd.workerHisLoc}/${id}`);
  }

  // onValueChange(value: Date): void {
  //   console.log(value[0].getTime()+","+value[1].getTime());
  // }
  // onDate1ValueChange(value: Date): void {
  //   this.bsRangeValue[0] = value;
  //   console.log(value);
  // }

  // onDate2ValueChange(value: Date): void {
  //   this.bsRangeValue[1] = value;
  //   console.log(value);
  // }

  getWorkerHistLoc(url:string){
    this.callApi.getData(url)
    .subscribe((data)=>{
      let hisLoc=data["0"].w_loc;
      this.classifyWorkerHistLoc(hisLoc);
      this.options.center=this.workerHistTrail[this.workerHistTrail.length-1].latLng;
      this.dataLoaded=true;
    })
  }

  classifyWorkerHistLoc(hist:Array<any>){
    console.log(hist);
    hist.forEach((loc)=>{
      let latLng=L.latLng(loc.latitude,loc.longitude);
      this.workerHistTrail.push({"timestamp":this.datePipe.transform(loc.timestamp, 'yyyy/MM/dd HH:mm:ss'),"latLng":latLng});
      // this.workerHistLatLng.push(latLng);
      // this.workerHistTimeStamp.push(this.datePipe.transform(loc.timestamp, 'yyyy/MM/dd HH:mm:ss'));
    });
  }

  clickLatLng(timestamp,latLng:L.latLng){
      let zoom=20;
      console.log(latLng);
      console.log(this.map);
      if(latLng){
          this.map.setView(latLng,zoom);
          this.showPopup(timestamp,this.historyLayers);
      }else{
          alert("User location not found");
      }
  }

  showPopup(timestamp,lyrs){
      let layers=lyrs.getLayers();
      for(let lyr of layers){
          console.log(lyr);
          let text = lyr.getPopup().getContent().replace(/(<\/?[^<]*>)/g,"").trim();
          // console.log(worker_id);
          // console.log(w_id);
          if(timestamp==text){
              console.log("same");
              lyr.openPopup();
          }
          
      }
  }

  options={
    layers:[L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png')],
    zoom:8,
    center: L.latLng(46.879966, -121.726909)
  };

  markerOptions = {  //required for having icon
    icon: L.icon({
      iconSize: [ 25, 41 ],
      iconAnchor: [ 13, 41 ],
      iconUrl: '/assets/map/marker-icon.png'
    })
  };


  onMapReady(map: L.Map){
      // this.latlngs.forEach(place=>{
      //   this.newMarker(place,this.markerOptions,"hello").addTo(map);
      // })
      this.map = map;
      let workerHistLatLng = [];
      for (var i = 0; i < this.workerHistTrail.length; i++) {
        this.newMarker(this.workerHistTrail[i].latLng,this.markerOptions,this.workerHistTrail[i].timestamp).addTo(this.historyLayers);
        workerHistLatLng.push(this.workerHistTrail[i].latLng);
      }
      this.map.addLayer(this.historyLayers);
      let polyline = L.polyline(workerHistLatLng,{color:'blue'}).addTo(map);
      this.map.fitBounds(polyline.getBounds());

      console.log('map is ready');
  }

    /* Add new stuff */
    newMarker(latlng, options, popup: string): L.Marker{
        let marker = L.marker(latlng, options);
        if(popup != null){
            marker.bindPopup(popup);
        }
        return marker;
    }

}



