import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';

@Component({
	selector: 'app-project-info',
	templateUrl: './project-info.component.html',
	styleUrls: ['./project-info.component.scss']
})
export class ProjectInfoComponent implements OnInit,OnChanges {
	@Input()
	project:object;
	dayElapsed:number;
	week: number;
	day: number;

	oneday: number = 1000 * 60 * 60 * 24;
	constructor() { }

	ngOnInit() {
	}

	//not using yet
	ngOnChanges(changes:SimpleChanges){
		if(changes['project']){
			this.dayElapsed=+(new Date(Date.now())).getTime() - +(new Date(this.project['p_data']['p_start_date'])).getTime();
			this.day = Math.floor(this.dayElapsed / (this.oneday));
			this.week = Math.floor(this.day / 7);
			this.day = this.day - 7 * this.week;
		}
	}

}



