import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';

@Component({
  selector: 'app-projects-table',
  templateUrl: './projects-table.component.html',
  styleUrls: ['./projects-table.component.scss']
})
export class ProjectsTableComponent implements OnInit,OnChanges {
	@Input()//input the projects array from the element of projects that use this table
	projects:any[];
  //configuration for feedback message
  formSubmitted:boolean;
  indicationMessage:Array<string>=["Success","NewProjectFormsIsSubmitted"];
  //whether the model of app-add-project is being display or not
	displayModel:boolean=false;
	filterQuery:string="";
	rowsOnPage:number = 10;
	sortBy:string = "p_id";
	sortOrder:string = "asc";
  constructor() { }

  ngOnInit() {
  }

  ngOnChanges(simpleChange:SimpleChanges){
    if(simpleChange['projects']){
      // console.log("input of projects in projects tables changes");
      console.log(this.projects);
    }
  }

  openModalBox():void{
  	this.displayModel=true;
  }

  closeModalBox():void{
  	this.displayModel=false;
  }

  
  //accept eventEmitter to let use know form submitted
  formSubmit(event){
    this.displayModel=false;
    this.formSubmitted=event;
  }
  
}



