import { Component, OnInit,Output,EventEmitter, ViewEncapsulation, Input, ViewChild  } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CallApiService, ApiCmd, ApiReloadService, CustomValidators } from "../../../shared/services";


@Component({
	selector: 'app-add-employee',
	templateUrl: './add-employee.component.html',
	encapsulation:ViewEncapsulation.None,
	styleUrls: ['./add-employee.component.scss']
})
export class AddEmployeeComponent implements OnInit {
	employeeForm:FormGroup;
	newid:string = "";
	@Output()
	formSubmitSuccess=new EventEmitter<boolean>();
	//to keep the file
	photo:any;
	file:any;
	@ViewChild('fileInput')
	fileInput: any;
	//dropdown list
	projectList: any[];
	projects:Array<any> = [];
	positionList: any[];
	position:Array<any> = [];
	constructor(private fb:FormBuilder,
		private callApi:CallApiService,
		private reloadService:ApiReloadService,
		) {
		this.getProjectList(ApiCmd.projectListOngoing);
		this.assignPositionDropdown();
	}

	ngOnInit() {
		this.employeeForm = this.fb.group({
			name: ['',[Validators.required,Validators.maxLength(50)]],
			phone: ['',[Validators.required,Validators.minLength(8),Validators.maxLength(15)]],
			email: ['',[Validators.required,Validators.email,Validators.maxLength(50)]],
			projectID:[''],
			address: ['',[Validators.required,Validators.maxLength(80)]],
			age: ['',[Validators.required,Validators.maxLength(50)]],
			position: ['',[Validators.required]],
			blood: ['',[Validators.required,CustomValidators.validateBloodType,Validators.maxLength(3)]],
			emergencyContactName:['',[Validators.required,Validators.maxLength(50)]],
			emergencyContactPhone: ['',[Validators.required,Validators.minLength(8),Validators.maxLength(15)]],
			height: ['',[Validators.required,Validators.min(0),Validators.max(280)]],
			weight: ['',[Validators.required,Validators.min(10),Validators.max(200)]],
			devid: ['',[Validators.required]]
		});
	}

	fileUpload(event){
		//preview
		if (event.target.files && event.target.files[0]&&this.isImage(event)) {
		  var reader = new FileReader();
		  reader.readAsDataURL(event.target.files[0]);

		  reader.onload = (event:any) => {
		    this.file = event.target.result;
			}
			this.photo=event.srcElement.files;
			console.log(this.photo);


		}else{
			alert("Please upload image only");
			this.fileInput.nativeElement.value = "";
		}

	}

	isImage(event):boolean{
		let file =event.target.files[0];
		let fileType = file["type"];
		fileType=fileType.split("/");
		if(fileType[0]=="image"){
			return true;
		}

		return false;
	}

	assignPositionDropdown():void{
		this.positionList = ['User'];
		this.positionList.forEach((position) => {
			this.position.push({
				id: position,
				text: position
			});
		});
		console.log(this.position);
	}

	getProjectList(url){
		this.callApi.getData(url)
		.subscribe(data=>{
			this.projectList=data;
			this.assignProjectToDropdown();
		})
	}

	assignProjectToDropdown(): void {
		this.projectList.forEach((project) => {
			this.projects.push({
				id: project.p_id,
				text:project.p_data.p_name
			});
		});
	}

	private value:any = {};

	public selected(value:any):void {
		this.employeeForm.controls["projectID"].patchValue(value.id);
		console.log('Selected value is: ', value);
	}

	public selectedPosition(value:any):void{
		this.employeeForm.controls["position"].patchValue(value.text);
		console.log('Selected position is: ', this.employeeForm.controls['position']);
	}

	public getLatestWorkerId(url,callback){
		this.callApi.getData(url)
			.subscribe(data=>{
				if(data.length > 0){
					this.newid = data[data.length-1]['w_id'];
					this.newid = "W"+(parseInt(this.newid.substr(1))+1);
				}else{
					this.newid = "W1";
				}
				callback();
			});
	}


	onSubmit(value){
		let self = this;
		let postData = function(){
			console.log(value);
			value['w_id'] = self.newid;
			self.callApi.postWithFile(ApiCmd.newEmployee,value,self.photo)
			.subscribe((response)=>{
				self.formSubmitSuccess.emit(true);
				console.log('response: '+response);
				self.reloadService.setReload()
			});
		};
		this.getLatestWorkerId(ApiCmd.workerList,postData);
		
	}

}





