import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { TabService } from "../../shared/services";
import { CallApiService, ApiCmd } from "../../shared/services";

@Component({
  selector: 'app-report',
  templateUrl: './report.component.html',
  encapsulation:ViewEncapsulation.None,
  styleUrls: ['./report.component.scss']
})
export class ReportComponent implements OnInit {
	report:any[];//=mock.data;
	summaryReport:any[];
	currentReport:string='All';
	currentTab:string="All";
	//dropdown list
	projectList: any[];
	projects:Array<any> = [];
	init:any=[];
	constructor(private tabService:TabService,private callApi:CallApiService) {
			this.tabService.currentProjectTab$.subscribe(
			tab=>{
				//set currentTab to clicked tab from tab.services.
				this.currentTab=tab;
			});
	}

	ngOnInit() {
		this.getProjectList(ApiCmd.projectList);
		// this.getReportList(ApiCmd.reportAll);
		// this.getReportSummary(ApiCmd.reportSummary);
		// console.log("report "+this.report);
	}

	// get report list
	getReportList(url:string){
	  this.callApi.getData(url)
	    .subscribe(data=>{
	      this.report=data;
	      })
	}

	//get summary report
	getReportSummary(url:string){
		this.callApi.getData(url)
			.subscribe(data=>{
				this.summaryReport=data;
			})
	}

	getProjectList(url){
		this.callApi.getData(url)
		.subscribe(data=>{
			this.projectList=data;
			this.assignProjectToDropdown();
		})
	}

	assignProjectToDropdown(): void {
		this.projects=[];
		this.projects.push(
			{
				id:"All",
				text: "All"
			}
		);
		this.projectList.forEach((project) => {
			this.projects.push({
				id: project.p_id,
				text:project.p_data.p_name
			});
		});
		this.init.push(this.projects[0]);
	}

	public selected(value:any):void {
		this.currentReport = value.id;
		// this.getReportSummary(`${ApiCmd.reportSummary}${value.id}`);
		// console.log('Selected value is: ', value.id);
	}

}

