import { environment } from '../../../../environments/environment';

const apiDom = environment.apiUrl;
// apiUrl:"http://wecarev2.mybluemix.net/api/"
// apiUrl: 'http://localhost/wecare/api/'
export class ApiCmd{
	static getApiCallerInfo:string="https://ipapi.co/json/";
	//Login
	//GET
	static loginCredential:string=apiDom+'login/';
	static authenticate:string=apiDom+'auth/';

	//Dashboard
	//GET
	static injuryGraph:string=apiDom+"v1/graph";
	static dutyList:string=apiDom+"onduty";
	static dutyChart:string=apiDom+"ondutychart";
	//POST

	//Map
	//GET
	static map:string=apiDom+"list/project";
	//POST
	static geofencing:string=apiDom+"geofence/";
	static zoning:string=apiDom+"zone/";
	static wifi:string=apiDom+"wifi/";
	
	//Projects
	//GET
	static projectList:string=apiDom+"list/project";
	//attached projects
	static projectListAttached:string=apiDom+"project_attached";
	//ongoing porjects
	static projectListOngoing:string=apiDom+"project_ongoing";
	//past projects
	static projectListPast:string=apiDom+"project_past";
	static managerList:string= apiDom+"list/user";
	//get the managerList to assign to project
	// static noProjectWorker:string=apiDom+"list/worker_free"

	//POST
	static newProject:string=apiDom+"project/add";
	static addOrRemoveWorker:string=apiDom+"worker/editproject";//add or remove worker
	static updateWorker:string=apiDom+"worker/edit";
	static removeWorker:string=apiDom+"worker/remove";

	//Employee
	//GETList
	static workerList : string = apiDom+"list/worker";
	static workerHisLoc:string=apiDom+"location";
	//POST
	static newEmployee: string = apiDom+"worker/add";
	static newAdmin:string=apiDom+"user/add";

	//Report
	//GET
	static reportAll : string = apiDom+"list/report/";
	static reportSummary: string=apiDom+"list/report/"
	//POST

	//Alert
	//GET
	static numList:string=apiDom+"unsolved/num";
	static sosList:string=apiDom+"unsolved/sos";
	static alertList:string=apiDom+"unsolved/alert";
	// /{{id}}
	static resolveAlert:string=apiDom+"alert/resolve";
	// /{{id}}
	static resolveSOS:string=apiDom+"sos/resolve";
	//POST

	//Settings page
	//GET
	//POST
	static editProfile:string=apiDom+"user/edit";

	//Device commmand
	static sendCommand:string=apiDom+"command/";
}


