import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';

@Injectable()
export class TabService {
	//Observable string sources
	private currentProjectTab=new Subject<string>();
  public currentProject:string;
  //Observable string streams
  currentProjectTab$=this.currentProjectTab.asObservable();

  //Observable string sources in view-project
  private currentViewProjectTab=new Subject<string>();
	//Observable string streams
  currentViewProjectTab$=this.currentViewProjectTab.asObservable();


  constructor() { 
  }

  setTab(tab:string){
  	//set the clicked tab from header
  	this.currentProjectTab.next(tab);
    this.currentProject=tab;
  }

  setProjectViewTab(tab:string){
    this.currentViewProjectTab.next(tab);
  }
}



