import { Component, OnInit, Input } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
declare var google: any;

@Component({
	selector: 'app-project-map',
	templateUrl: './project-map.component.html',
	styleUrls: ['./project-map.component.scss']
})
export class ProjectMapComponent implements OnInit {

	constructor() { }
  //google map variable
  map: any;
  searchBox: any;

  //other variable
  location:string;
	@Input()
	project:object;
  //polygon array with location only
  @Input()
  polygonArrays:any[]=[];
  //polygon arrays already initialized with setPolygon function
  initializedPolygonArrays:any[]=[];
	// myLatLng:object = {lat: 3.14, lng: 103.3};

  
	ngOnInit() {
		//initialize the project location in map
		let lat=+this.project['p_latitude'];
		let long=+this.project['p_longitude'];
		this.location=this.project['p_location'];
		if(lat!=null&&long!=null){
			console.log(`lat: ${lat},long: ${long}`);
			this.initMap(lat,long);
			this.createManyMarkers();	
		}
	}

	//create workers markers
	createManyMarkers(){
		this.project['p_workers'].forEach((worker)=>{
			let coordinate={lat: +worker.latitude,lng: +worker.longitude } as object;
      let latlng=new google.maps.LatLng(+worker.latitude,+worker.longitude);
      console.log(latlng);
      this.createOneMarker(coordinate,worker.u_name);
    })
  }

  //add one marker to the map
  createOneMarker(latlong,name){
    let marker = new google.maps.Marker({
        position: latlong,
        map: this.map,
        title: name
      });
      // if(google.maps.geometry.poly.containsLocation(marker.getPosition(), this.initializedPolygonArrays[0])){
      //   alert('inside');
      // }
	}

	initMap(latitude, long) {
		this.map = new google.maps.Map(document.getElementById('map'), {
			center: { lat: latitude, lng:long  },
			zoom: 8
		});

		let projectLocation=new google.maps.Marker({
          map: this.map,
          title: this.project['p_id'],
          position: { lat: latitude, lng: long }
        })

		// Create the search box and link it to the UI element.
		let input = document.getElementById('pac-input');
		this.searchBox = new google.maps.places.SearchBox(input);
		this.map.controls[google.maps.ControlPosition.TOP_LEFT].push(input);

        projectLocation.addListener('click', function() {
          this.map.setZoom(25);
          this.map.setCenter(projectLocation.getPosition());
        });

        // Construct the polygon.
        this.polygonArrays.forEach((poly)=>{
          this.setPolygon(poly.location,poly.zoneName);
        });

	}

	setPolygon(polygon:any[],zone:string){
		let area = new google.maps.Polygon({
          paths: polygon,
          strokeColor: '#FF0000',
          strokeOpacity: 0.8,
          strokeWeight: 2,
          fillColor: '#FF0000',
          fillOpacity: 0.35
        });
        area.setMap(this.map);
        this.initializedPolygonArrays.push(area);
        // Add a listener for the click event.
        area.addListener('click', this.showZone(polygon[0],zone));

	}

  //zoom in to the location clicked
	showZone(coordinate:any,zone:string):void {
        let infoWindow = new google.maps.InfoWindow;
        // Replace the info window's content and position.
        infoWindow.setContent("Zone: "+zone);
        infoWindow.setPosition(coordinate);

        infoWindow.open(this.map);
      }

}



