import { Component, OnInit, Input, SimpleChanges,OnChanges} from '@angular/core';
import { colorSets } from './colorScheme'
import { CallApiService, ApiCmd } from "../../../shared/services";
import { Observable } from 'rxjs/Observable'

@Component({
	selector: 'app-health-visual',
	templateUrl: './health-visual.component.html',
	styleUrls: ['./health-visual.component.scss']
})
export class HealthVisualComponent implements OnInit,OnChanges {
	@Input()
	display:string = '';
	summaryReport:any[] = [{heartrate: '', health_status: '', working: ''}];

	dataFilled:boolean=false;
	hrArray:Chart;
	healthStatusArray:Chart;
	atWorkArray:Chart;
	intervalHook;
	interval = 3000;

	constructor(private callApi:CallApiService) { 

	}

	ngOnChanges(simpleChanges:SimpleChanges){
		if(simpleChanges['summaryReport'] || simpleChanges['display']){
			// console.log('changes: '+this.display);
			this.updateChart(this.display);
		}
	}

	ngOnInit() {
		this.callApi.getData(ApiCmd.reportSummary+this.display).subscribe(data=>{this.summaryReport=data;this.dataFilled=true});
		this.intervalHook = setInterval(()=>{
			// console.log("report reload: "+this.display);
			this.updateChart(this.display);
		},this.interval);
	}

	ngOnDestroy(){
		clearInterval(this.intervalHook);
	}

	updateChart(display: string): void{
		this.callApi.getData(ApiCmd.reportSummary+display)
			.subscribe(data=>{
				if(!this.compareJSONArray(data[0].heartrate,this.summaryReport[0].heartrate)){this.summaryReport[0].heartrate=data[0].heartrate; this.getHrChart(); }
				if(!this.compareJSONArray(data[0].health_status,this.summaryReport[0].health_status)){this.summaryReport[0].health_status=data[0].health_status; this.getStatusChart();}
				if(!this.compareJSONArray(data[0].working,this.summaryReport[0].working)){this.summaryReport[0].working=data[0].working; this.getPresentChart();}

		});
	}

	getHrChart(): void {
		let heartrate=this.summaryReport[0].heartrate;
		let arr=[];
		arr.push({name:'Normal bpm', value:+heartrate.normal});
		arr.push({name:'>120 bpm', value:+heartrate.high});
		arr.push({name:'<60 bpm', value:+heartrate.low});
		this.hrArray= new Chart(arr);
	}

	getStatusChart(): void {
	// data.push(['OK', +this.healthStatus.ok]);
	// 	data.push(['Unhealthy', +this.healthStatus.unhealthy]);
		let stat=this.summaryReport[0].health_status;
		let arr=[];
		arr.push({name:'OK', value:+stat.ok});
		arr.push({name:'Unhealthy', value:+stat.unhealthy});
		this.healthStatusArray= new Chart(arr);
		this.healthStatusArray.explodeSlices=true;
		this.healthStatusArray.doughnut=true;
		this.healthStatusArray.arcWidth=0.5;
	}
	getPresentChart(): void {
		let work=this.summaryReport[0].working;
		let arr=[];
		arr.push({name:'Present', value:+work.present});
		arr.push({name:'Absent', value:+work.absent});
		this.atWorkArray= new Chart(arr);
		this.atWorkArray.view=[1024,400];
		// this.atWorkArray.colorScheme=colorSets[2];
	}

	compareJSONArray(arr1, arr2): boolean{
		if(arr1 || arr2){
			if(Object.keys(arr1).length == Object.keys(arr2).length){
				let keys = Object.keys(arr1);
				for(let i=0;i<keys.length;i++){
					if( !arr2.hasOwnProperty(keys[i]) ) { return false }
					if( arr1[keys[i]] != arr2[keys[i]] ){ return false }
				}
				return true;
			}
		}
		return false;
	}

}

export class Chart {
	data: any[];

	view: any[] = [400, 250];

	// options
	showLegend = true;

	colorScheme = {
		domain: ['#5AA454', '#A10A28', '#C7B42C', '#AAAAAA']
	};

	arcWidth = 0;

	// pie
	showLabels = true;
	explodeSlices = false;
	doughnut = true;

	constructor(data:any[]) {
		this.data=data;
	}

	onSelect(event) {
		console.log(event);
	}
  
}

