import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse} from '@angular/common/http';
import { RequestOptions,Http, Headers,Response, RequestMethod } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { ApiCmd } from '../call-apis/api-cmd';
import { AuthService } from '../../auth/auth.service';

@Injectable()
export class CallApiService {

	constructor(private http: HttpClient,private http2: Http) { }
	headers:Headers;
	options:RequestOptions;
	userData;

	logout(){
		this.userData = null;
	}

	getCompanyID(){
		return this.userData[0].company_id;
	}

	getData(url):Observable<any>{
		if(this.userData == null) this.userData = JSON.parse(sessionStorage.getItem('currentUser')); 
		// if(this.userData != null) console.log(this.userData[0].company_id);
		if(url.search('login') < 0){
			if(url.search('localhost') >= 0) url = url.replace('api/','api/'+this.userData[0].company_id+'/');
			else if(url.search('mmdt') >= 0) url = url.replace('api.php/','api.php/'+this.userData[0].company_id+'/');
		} 
	  	return this.http.get(url)
			// .do(response=>{console.log('All: ' +JSON.stringify(response))})
			.catch(this.handleError);
	}

	postData(url:string, postData:any):Observable<any>{
		// let headers = new Headers({'Content-Type':'application/json'});
	  	// let options = new RequestOptions({ headers: headers});
	  	let formData:FormData = new FormData();

	    postData = JSON.parse(postData);
    	if(postData !=="" && postData !== undefined && postData !==null){
			for (var property in postData) {
				if (postData.hasOwnProperty(property)) {	
					formData.append(property, postData[property]);
				}
			}
			formData.append('c_id', this.userData[0].company_id);
			return this.http2.post(url,formData,{headers: new Headers()}).map((res:Response)=>{return res});
		}
    }


    //Dunno how to inspect formData
    postWithFile(url: string, postData:any,files:File[]): Observable<any>{
    	let formData:FormData = new FormData();
	    if(files!=null)
	    formData.append('files', files[0], files[0].name);
	    // For multiple files
	    // for (let i = 0; i < files.length; i++) {
	    //     formData.append(`files[]`, files[i], files[i].name);
	    // }

	    if(postData !=="" && postData !== undefined && postData !==null){
			for (var property in postData) {
				if (postData.hasOwnProperty(property)) {	
					formData.append(property, postData[property]);
				}
			}
			formData.append('c_id', this.userData[0].company_id);
	      
			return this.http2.post(url, formData, {
				headers: new Headers()
			}).map((res:Response)=>{return res});
	            // .subscribe(
	      //           res => {
	      //             this.responseData = res.json();
	      //             resolve(this.responseData);
	      //           },
	      //           error => {
	      //             this.router.navigate(['/login']);
	      //             reject(error);
	      //           }
	      //       );
          }//);
	          // return returnReponse;
    }

	private handleError(error: HttpErrorResponse):Observable<any>{
		console.log('An error occurred',error);
		return Observable.throw(error.message);
	}


	sendCommand(command:any, dev_id: string):Observable<any>{
		let commandJson = {
			command: command
		};
		if(dev_id!=='' && dev_id!==null){
			commandJson['device'] = dev_id;
		}
		console.log(commandJson);
		return this.http2.post(ApiCmd.sendCommand, JSON.stringify(commandJson),{headers: new Headers(), method: RequestMethod.Post}).map((res:Response)=>{return res});
	}


      //function to get location from lat lng
	getLocationByLatLng(http, location, group):Observable<any>{
		var url = "https://maps.google.cn/maps/api/geocode/json?latlng=";
		var key = "&key=AIzaSyAeg8uvtlLzc1zwyNL1z_UXZwZzjBw0B2g";
		url = url.concat(location.lat,',',location.lng,key);
		return Observable.create(observer => {
			getData(http, url).subscribe(
				data => {
				  observer.next(extractLocation(data));
				}
			);
		}); 

		function getData(http,url): Observable<string>{
			return   http.get(url)
				// .do(data => console.log())
				.catch(err => Observable.throw(err));
		}

		function extractLocation(result): string{
			var body = JSON.parse(result._body);
			// console.log(body);
			if( 'error_message' in body){
				console.log(body.error_message);  //log the error
			}else{
				let address =  body.results[0].formatted_address;
				// console.log(address);
				return address;
			}
		}
	}
}




