import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { CallApiService, ApiCmd,ApiReloadService } from "../../../shared/services";

@Component({
	selector: 'app-collapsible',
	templateUrl: './collapsible.component.html',
	styleUrls: ['./collapsible.component.scss']
})
export class CollapsibleComponent implements OnInit {
	isCollapsed:Array<boolean>;
	allProjects:any;
	@Output()
	workerLocation=new EventEmitter<object>();

	constructor(private callApi:CallApiService) { }

	ngOnInit() {
		this.getAllList(ApiCmd.projectList);
	}

	getAllList(url:string){
		this.callApi.getData(url)
			.subscribe(data=>{
				this.allProjects=data;
				console.log(this.allProjects);
				this.isCollapsed=new Array<boolean>(this.allProjects.length).fill(false);
				console.log(this.isCollapsed);
	  		})
	}

	showLoc(lat,lng,w_id){
		console.log(lat,lng);
		this.workerLocation.emit({'lat':lat,'lng':lng,'w_id':w_id});
	}

	userOnline(lastReceiveTime):boolean{
		var oneMinute:number = 1000 * 60;
		var timeDifferent = +(new Date(Date.now())).getTime() - +(new Date(lastReceiveTime)).getTime()
		var timeDifferentInMin = timeDifferent/oneMinute;
		console.log(timeDifferentInMin);
		if(timeDifferentInMin<=30)
			return true;
		else
			return false;
	}

}



