import { Component,OnInit,Input,ViewEncapsulation } from '@angular/core';
import { trigger, state, style, animate, transition } from '@angular/animations';

@Component({
  selector: 'app-home-layout',
  templateUrl: './home-layout.component.html',
  encapsulation:ViewEncapsulation.None,
  styles: ['./home-layout.scss'],
  animations:[
  //animation of sidebar
    trigger('showIt',[
        state('showAll',style({
          transform:'translate3D(0,0,0)'
        })),
        state('showIcon',style({
          transform:'translate3D(-60%,0,0)'
        })),
        transition('showAll=>showIcon',animate('400ms ease-in-out')),
        transition('showIcon=>showAll',animate('400ms ease-in-out'))
      ]),
    //animation of content when sidebar move
    trigger('move',[
        state('sideBarWithDesc',style({
          padding: "0 0 0 200px"
        })),
        state('sideBarWithIcon',style({
          padding: "0 0 0 80px"
        })),
        transition('sideBarWithDesc=>sideBarWithIcon',animate('400ms ease-in-out')),
        transition('sideBarWithIcon=>sideBarWithDesc',animate('400ms ease-in-out'))
      ])
  ]
})
export class HomeLayoutComponent implements OnInit {
  show:string='showAll';
  move:string='sideBarWithDesc';
  constructor() { }

  ngOnInit() {
  }

  //for changng the sidebar state and content state to trigger animation
  onShow(show:string){
    //show manage the animation of the content
    this.show=show;
    //move manage the animation of the sidebar
    this.move=this.move==='sideBarWithDesc'?'sideBarWithIcon':'sideBarWithDesc';
  }

}



