import { Component, OnInit,Output,EventEmitter, ViewEncapsulation, Input, ViewChild  } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CallApiService, ApiCmd, ApiReloadService, CustomValidators } from "../../shared/services";
import { sha256 } from 'js-sha256';

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.scss']
})
export class FormComponent implements OnInit {

  @Input() formData:any;
  @Output()
  formSubmitSuccess=new EventEmitter<boolean>();
  formForm:FormGroup;
  data:any;
  gotImage:boolean;
  url:string;
  //to keep the file
  photo:any;
  file:any;
  @ViewChild('fileInput')
  fileInput: any;
  constructor(private callApi:CallApiService) { }
  ngOnInit() {
  	this.formForm=this.formData[0].form_group;
  	this.data=this.formData[0].form_control_names;
  	this.gotImage=this.formData[0].contain_image;
  	this.url=this.formData[0].submit_url;
  	console.log(this.formForm);
  	console.log(this.data);
  }

  onSubmit(event:any):void{
  	// this.employeeForm.controls["projectID"].patchValue(value.id);
  	// event.password=sha256(event.password);
  	console.log(event);
  	console.log(this.gotImage);
  	if(this.gotImage)this.gotImageSubmit(event);
  	else this.noImageSubmit(event);
  }

  gotImageSubmit(event){
  	this.callApi.postWithFile(this.url,event,this.photo)
  	.subscribe((response)=>{
  		this.formSubmitSuccess.emit(true);
  		console.log('response: '+response);
  	});
  }

  noImageSubmit(event){
  	this.callApi.postData(this.url,JSON.stringify(event))
  	.subscribe((response)=>{
  		this.formSubmitSuccess.emit(true);
  		console.log('response: '+response);
  	})
  }

  fileUpload(event){
  	//preview
  	if (event.target.files && event.target.files[0]&&this.isImage(event)) {
  	  var reader = new FileReader();
  	  reader.readAsDataURL(event.target.files[0]);

  	  reader.onload = (event:any) => {
  	    this.file = event.target.result;
  		}
  		this.photo=event.srcElement.files;
  		console.log(this.photo);


  	}else{
  		alert("Please upload image only");
  		this.fileInput.nativeElement.value = "";
  	}

  }

  isImage(event):boolean{
  	let file =event.target.files[0];
  	let fileType = file["type"];
  	fileType=fileType.split("/");
  	if(fileType[0]=="image"){
  		return true;
  	}

  	return false;
  }

}



