export class Profile{
	_id: string;
    _rev: string;
    u_id: string;
    type: string;
    u_login: Login;
    pw: string;
    u_info:Info;
    u_name: string;
    details?:Details;
    project: Project;
}

export class Login{
    u_pw: string;
    u_email: string;
}

export class Info{
    u_photo: string;
    u_contact: string;
    u_age: string;
    u_position: string;
}

export class Project{
	p_id: string;
    p_name: string;
    p_location: string;
}

export class Details{
	u_bloodtype: string;
	u_height: string;
	u_weight: string;
}


