import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CallApiService, ApiCmd } from "../../../shared/services";
import { Profile } from "../../../shared";
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Location } from '@angular/common';
import { Http } from '@angular/http';
import { EmployeeHistoryMapComponent } from "./employee-history-map/employee-history-map.component";

@Component({
	selector: 'app-view-employee',
	templateUrl: './view-employee.component.html',
	styleUrls: ['./view-employee.component.scss']
})
export class ViewEmployeeComponent implements OnInit {
	//get the user data from authService 
	profile:Profile;
	//project this user currently working
	project;
	id:string;
	rowsOnPage:number = 5;
	sortBy:string = "w_id";
	sortOrder:string = "asc";
	sensorData:any[];
	editDetails:boolean=false;
	userForm:FormGroup;
	userFormValue:any;
	cannotEdit:boolean=false;
	//configuration for feedback message
	commandSend:boolean=false;
	indicationMessage:Array<string>;

	//boolean to show if user online
	online:boolean=null;

	intervalHook;

	//edit photo
	photo:any;
	file:any;
	@ViewChild('fileInput')
	fileInput: any;

	constructor(private _route: ActivatedRoute,
				private callApi: CallApiService,
		 		private fb:FormBuilder,
		 		private location:Location,
		 		private http:Http) {
	}

	ngOnInit() {
		this.id=this._route.snapshot.paramMap.get('id');
		this.getWorkerDetail(`${ApiCmd.workerList}/${this.id}`);

		this.intervalHook = setInterval(()=>{
			this.getWorkerDetail(`${ApiCmd.workerList}/${this.id}`);
		}, 20000);
	}

	ngOnDestroy(){
		clearInterval(this.intervalHook);
	}

	//call api to get this id worker's data
	getWorkerDetail(url: string) {
		this.callApi.getData(url)
		.subscribe(data => {
			this.profile = data[0];
			// console.log(this.profile);
			this.project = this.profile.project;
			this.sensorData = this.profile['w_sensor_data_'];
			//prepare the data to be edit
			if((this.profile['w_location']==='-' || this.profile['w_location']==='') && this.profile['w_dev_data']!=null && this.profile['w_dev_data'].latitude != null && this.profile['w_dev_data'].longitude != null){
				this.callApi.getLocationByLatLng(this.http,{lat:this.profile['w_dev_data'].latitude, lng:this.profile['w_dev_data'].longitude},null).subscribe(ditto=>{
					this.profile['w_location'] = ditto;
				});
			}
			this.online = this.userOnline(this.profile['dev_time']);
			this.initData(this.profile);
			console.log(this.profile['w_id']);
			// console.log(this.sensorData);
		});
	}

	edit(){
		this.editDetails=true;
		console.log("editDetails clicked");
	}

	removeButtonPressed(){
		if(confirm(`Confirm to delete ${this.id}`)){
			this.removeWorker(this.id);
			console.log(`${this.id} is confirm to remove`);
		}

	}

	removeWorker(workerId:string){
		let removeData:object={w_id: workerId};
		console.log(JSON.stringify(removeData));
		this.callApi.postData(ApiCmd.removeWorker,JSON.stringify(removeData))
		.subscribe((response)=>{
			console.log(response);
			this.location.back();
		})
	}

	initData(profile:any){
		this.userForm = this.fb.group({
			id:[profile.w_id],
			name:[profile.w_name_photo.w_name],
			age:[profile.w_info.w_age],
			emailAddress:[profile.w_info.w_email],
			contact:[profile.w_info.w_contact],
			position:[profile.w_info.w_position],
			emergencyContactName:[profile.w_emergency.emergencyContactName],
			emergencyContactPhone: [profile.w_emergency.emergencyContactPhone],
			deviceID:[profile.dev_id]

		});
		// ,[Validators.required,Validators.maxLength(50)]
		// ,[Validators.required,Validators.minLength(8),Validators.maxLength(15)]
	}

	onSubmit(event:any):void{
		// this.employeeForm.controls["projectID"].patchValue(value.id);
		// event.password=sha256(event.password);
		console.log(event);
		console.log(this.photo);
		if(this.photo!=null)this.gotImageSubmit(event);
		else this.noImageSubmit(event);
	}
  
	gotImageSubmit(event){
		this.userFormValue = event;
		this.callApi.postWithFile(ApiCmd.updateWorker,this.userFormValue,this.photo)
		.subscribe((response)=>{
			this.getWorkerDetail(`${ApiCmd.workerList}/${this.id}`);
		});
		this.editDetails=false;
	}
  
	noImageSubmit(event){
		// value['id'] = this.profile.w_id;
		this.userFormValue = event;
		console.log(JSON.stringify(this.userFormValue));
		this.callApi.postData(ApiCmd.updateWorker,JSON.stringify(this.userFormValue)).subscribe(
				(response)=>{
					//re-retrieve user data
					this.getWorkerDetail(`${ApiCmd.workerList}/${this.id}`);
				}
			)
		this.editDetails=false;
	}
  
	fileUpload(event){
		//preview
		if (event.target.files && event.target.files[0]&&this.isImage(event)) {
		  var reader = new FileReader();
		  reader.readAsDataURL(event.target.files[0]);
  
		  reader.onload = (event:any) => {
			this.file = event.target.result;
			}
			this.photo=event.srcElement.files;
			console.log(this.photo);
  
  
		}else{
			alert("Please upload image only");
			this.fileInput.nativeElement.value = "";
		}
  
	}
  
	isImage(event):boolean{
		let file =event.target.files[0];
		let fileType = file["type"];
		fileType=fileType.split("/");
		if(fileType[0]=="image"){
			return true;
		}
  
		return false;
	}

	//to signified if command send successfully
	receiveSentStatus(event:object){
		this.indicationMessage=event['indicationMessage'];
		this.commandSend=event['commandSend'];
	}

	userOnline(lastReceiveTime):boolean{
		var oneMinute:number = 1000 * 60;
		var timeDifferent = +(new Date(Date.now())).getTime() - +(new Date(lastReceiveTime)).getTime()
		var timeDifferentInMin = timeDifferent/oneMinute;
		if(timeDifferentInMin<=30)
			return true;
		else
			return false;
	}

}


