import { Component, OnInit } from '@angular/core';
import { CallApiService, ApiCmd } from "../../shared/services";
import { Http } from "@angular/http";

@Component({
  selector: 'app-alert',
  templateUrl: './alert.component.html',
  styleUrls: ['./alert.component.scss']
})
export class AlertComponent implements OnInit {
	rowsOnPage:number = 10;
	sortBy:string = "w_id";
	sortOrder:string = "asc";
  filterQuery:string = "";
	alerts:any[] = [];
  sos:any[] = [];
  intervalHook;
  interval = 1000;


  constructor(private CallApi:CallApiService, private http:Http) { }

  ngOnInit() {
    this.getSosList(ApiCmd.sosList);
    this.getAlertList(ApiCmd.alertList);
    this.intervalHook = setInterval(()=>{
      this.getSosList(ApiCmd.sosList);
      this.getAlertList(ApiCmd.alertList);
    }, this.interval);
  }

  ngOnDestroy(){
    clearInterval(this.intervalHook);
  }

  // get sos list
  getSosList(url:string){
    this.CallApi.getData(url)
      .subscribe(data=>{
        if(!this.compareJSONArray(this.sos, data)){
          for(let sos in data){
            if((data[sos].w_location==='-' || data[sos].w_location==='') && typeof data[sos].w_dev_data.latitude !== 'undefined' && typeof data[sos].w_dev_data.longitude !== 'undefined'){
              this.CallApi.getLocationByLatLng(this.http,{lat:data[sos].w_dev_data.latitude, lng:data[sos].w_dev_data.longitude},null).subscribe(ditto=>{
                // console.log(ditto);
                data[sos]['w_location'] = ditto;
              });
            }
          }
          this.sos=data;
        }
    });
  }

  // get alert list
  getAlertList(url:string){
    this.CallApi.getData(url)
      .subscribe(data=>{
        if(!this.compareJSONArray(this.alerts, data)){
          for(let alert in data){
            if((data[alert].w_location==='-' || data[alert].w_location==='') && typeof data[alert].w_dev_data.latitude !== 'undefined' && typeof data[alert].w_dev_data.longitude !== 'undefined'){
              this.CallApi.getLocationByLatLng(this.http,{lat:data[alert].w_dev_data.latitude, lng:data[alert].w_dev_data.longitude},null).subscribe(ditto=>{
                // console.log(ditto);
                data[alert]['w_location'] = ditto;
              });
            }
          }
          this.alerts=data;
        }
    });
  }

  //resolve sos
  resolveSOS(id:string){
    this.CallApi.postData(`${ApiCmd.resolveSOS}/`,JSON.stringify({'id': id}))
      .subscribe(response=>{
        this.getSosList(ApiCmd.sosList);
        console.log(response);
    })
  }

  //resolve alert
  resolveAlert(id:string){
    console.log(id);
    this.CallApi.postData(`${ApiCmd.resolveAlert}/`,JSON.stringify({'id': id}))
      .subscribe(response=>{
        this.getAlertList(ApiCmd.alertList);
        console.log(response);
    })
  }

  compareJSONArray(arr1, arr2): boolean{
    let isSame = false;
    if(arr1 || arr2){
      if(Object.keys(arr1).length === Object.keys(arr2).length){
        let keys = Object.keys(arr1);
        for(let i=0;i<keys.length;i++){
          if( !arr2.hasOwnProperty(keys[i]) ) {
            break;
          }
          if( arr1[keys[i]] !== arr2[keys[i]] ){
            if(Array.isArray(arr1[keys[i]]) && Array.isArray(arr2[keys[i]])){
              if(!this.compareJSONArray(arr1[keys[i]],arr2[keys[i]])){
                break;
              } 
            }else{
              break;
            }
          }
        }
        isSame = true;
      }
    }
    return isSame;
  }
}



