import { Component, OnInit, Input} from '@angular/core';
import { FormControl, FormGroup, FormBuilder, FormArray } from '@angular/forms';
import { CallApiService, ApiCmd } from "../../../../shared/services";

@Component({
  selector: 'app-project-workers',
  templateUrl: './project-workers.component.html',
  styleUrls: ['./project-workers.component.scss']
})
export class ProjectWorkersComponent implements OnInit {
  @Input() group: FormGroup;

  constructor(private callApi:CallApiService) {
    this.getWorkerList(ApiCmd.workerList);
  }
  employees:any[];
  selectedEmployees:any[]=[];
  ngOnInit() {
  	// this.employees=[{w_id:"w1033",w_name:"john legent"},{w_id:"w103",w_name:"john smith"},{w_id:"w93",w_name:"Hello"}]
  	console.log(this.employees);
  }

  toggleEmployeeSelection(employee) {
    const formArray: FormArray = this.group.get('projectWorkers') as FormArray;

    var idx = this.selectedEmployees.indexOf(employee);

    // Is currently selected
    if (idx > -1) {
      this.selectedEmployees.splice(idx, 1);
      // find the unselected element
      let i: number = 0;

      formArray.controls.forEach((ctrl: FormControl) => {
        if(ctrl.value == employee) {
          // Remove the unselected element from the arrayForm
          formArray.removeAt(i);
          return;
        }

        i++;
      });
    }

    // Is newly selected
    else {
      // Add a new control in the arrayForm
      formArray.push(new FormControl(employee));
      this.selectedEmployees.push(employee);
    }

   console.log(this.selectedEmployees);
 }

 getWorkerList(url){
   this.callApi.getData(url)
   .subscribe(data=>{
     this.employees=data;
   });
 }

}



