import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';

@Injectable()
export class ApiReloadService {
	private reload=new Subject<boolean>();
	reload$=this.reload.asObservable();
	constructor() { }

	setReload(){
		this.reload.next(true);
	}

}



