import { Component, OnInit,Input } from '@angular/core';
import { Data } from '../../../shared/';

@Component({
  selector: 'app-report-graph',
  templateUrl: './report-graph.component.html',
  styleUrls: ['./report-graph.component.scss']
})
export class ReportGraphComponent implements OnInit {
  accidentRate:any[];
  areaChartData:any;
  @Input()
  data:any[];
  constructor() { 
  	this.accidentRate=Data.accidentRate;
  }
  ngOnInit() {
  	this.getLineChart();
  }

  getLineChart():void{
		let areaData=[] as any;
		areaData.push(['year','percentage']);
		for (let accident of this.accidentRate) {
			areaData.push([accident.year,+accident.percentage]);
		}
		this.areaChartData =  {
		  chartType: 'AreaChart',
		  dataTable: areaData,
		  options: {
		  		'legend':'top',
		  		'height':600,
		  		'colors':['rgb(137,110,105)']
		  },
		};

	}

}



