import { Component, OnInit } from '@angular/core';
import { FormsModule, FormGroup, FormControl } from '@angular/forms';
import { CallApiService, ApiCmd, ApiReloadService } from "../../shared/services";
import { Http } from '@angular/http';

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.scss']
})
export class EmployeeComponent implements OnInit {
	defaultNull:string = "-";
	employees:any[];
	//configuration for feedback message
	formSubmitted:boolean=false;
	indicationMessage:Array<string>=["Success","NewEmployeeFormsIsSubmitted"];

	//whether the model of app-add-employee is being display or not
	displayModel:boolean=false;
	filterQuery:string="";
	rowsOnPage:number = 10;
	sortBy:string = "w_id";
	sortOrder:string = "asc";
	constructor(private CallApi:CallApiService,
              	private reloadService:ApiReloadService,
              	private http:Http
				) {
		this.reloadService.reload$.subscribe(
		  (reload)=>{
		    if(reload){
		    	// console.log("reload employees");
		    	this.employees=null;
		    	//reload list when new employee is added
				setTimeout(() => this.getList(ApiCmd.workerList), 2000);
		    }
		  }
		);

	}

	ngOnInit() {
		this.getList(ApiCmd.workerList);
	}

	// get worker list
	getList(url:string){
		this.CallApi.getData(url)
			.subscribe(data=>{
				for(let worker in data){
					if((data[worker]['w_location']==='-' || data[worker]['w_location']==='') && data[worker].w_dev_data!=null && data[worker].w_dev_data.latitude != null && data[worker].w_dev_data.longitude != null){
						this.CallApi.getLocationByLatLng(this.http,{lat:data[worker].w_dev_data.latitude, lng:data[worker].w_dev_data.longitude},null).subscribe(ditto=>{
							data[worker]['w_location'] = ditto;
						});
					}
				};
				this.employees=data;
	  		})
	}

	openModalBox():void{
		this.displayModel=true;
	}

	closeModalBox():void{
		this.displayModel=false;
	}

	//accept eventEmitter to let use know form submitted
	formSubmit(event){
		this.displayModel=false;
		this.formSubmitted=event;
	}
}



