import { Component, OnInit,Input,OnChanges, SimpleChanges } from '@angular/core';
import { BrowserModule} from '@angular/platform-browser';
import { BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { trigger, state, style, animate, transition } from '@angular/animations';
import { CallApiService, ApiCmd } from "../services";

@Component({
  selector: 'app-sidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.scss'],
  animations:[
    trigger('move',[
        state('showAll',style({
          'margin-left': '0px'
        })),
        state('showIcon',style({
          'margin-left': '125px'
        })),
        transition('showAll=>showIcon',animate('400ms ease-in-out')),
        transition('showIcon=>showAll',animate('400ms ease-in-out'))
      ]),
    trigger('title',[
        state('showAll',style({
        })),
        state('showIcon',style({
          'display': 'none'
        })),
        transition('true=>false',animate('400ms ease-in-out')),
        transition('false=>true',animate('400ms ease-in-out'))
      ])
    ]
})
export class SidenavComponent implements OnInit,OnChanges {

	@Input()
	show:string;

	//variable to control the variable sidenav title
	smallIcon=false;
  numberAlert = 0;
  intervalHook;

  constructor(private CallApi:CallApiService) { }

  ngOnInit() {
    this.CallApi.getData(ApiCmd.numList).subscribe((value)=>{
      this.numberAlert = value[0].alert_num + value[0].sos_num;
    });
    this.intervalHook = setInterval(()=>{
      this.CallApi.getData(ApiCmd.numList).subscribe((value)=>{
        this.numberAlert = value[0].alert_num + value[0].sos_num;
      });
    }, 3000);
  }

  ngOnChanges(changes:SimpleChanges){
  	if(changes['show']){
      console.log(this.show);
      if(this.show === 'showIcon')
	  		this.smallIcon=true;
	  	else
	  		this.smallIcon=false;
  	}
  }

  ngOnDestroy(){
    clearInterval(this.intervalHook);
  }

}



