import { Component, OnInit, OnChanges, Input } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CallApiService, ApiCmd, CustomValidators } from "../../shared/services";
import { Observable } from 'rxjs/Rx';
import * as L from 'leaflet';
import * as L_draw from 'leaflet-draw';

@Component({
	selector: 'app-map',
	templateUrl: './map.component.html',
	styleUrls: ['./map.component.scss']
})
export class MapComponent implements OnInit, OnChanges {
    @Input() height:any;
    @Input() collapsibleNeeded:boolean=true;

	constructor(private callApi:CallApiService, private _route:ActivatedRoute, private fb:FormBuilder) { }
	
    ngOnChanges(){
        if(this.height !== null && typeof this.height !== 'undefined'){
            document.getElementById('height').style.height = this.height;
        }
    }

    intervalHook;
    interval = 3000;

    //ngModel
    polygon_project_id_valid: any;
    polygon_type_valid:any;

	ngOnInit() {
        this.polygonForm = this.fb.group({
            polygon_project_id: ['',[Validators.required, CustomValidators.validateSelect]],
            polygon_type: ['',[Validators.required, CustomValidators.validateSelect]],
            polygon_zone_name: ['',[Validators.required]],
            polygon_zone_level: ['',[Validators.required]]
        });

        this.project_id = this._route.snapshot.paramMap.get('id');
        this.getDataFromAPI(this.callApi, this.project_id).subscribe(
            data => {
                this.loadFromStorage(data);
                this.modalBox_init(data);
                if(this.projectLayers.getLayers().length>0){
                    this.map.fitBounds(this.projectLayers.getBounds());    //automatically fit all project in view
                }else{
                    this.map.setView(this.initLatlng,this.initZoom);
                }
        });

        this.intervalHook = setInterval(()=>{
            this.getDataFromAPI(this.callApi, this.project_id).subscribe((data)=>{
                // this.clearMap();
                this.loadFromStorage_worker(data);
            });
        }, this.interval);
        
        

    }



    ngOnDestroy(){
        clearInterval(this.intervalHook);
    }

    /*   stores all the drawn polygons
    0 ... N - each new stuff
    [N].latlng -> array of latlng
    */
    project_id: string;
    editableLayers = new L.FeatureGroup();
    zoneLayers = new L.FeatureGroup();
    projectLayers = new L.FeatureGroup();
    workerLayers = new L.FeatureGroup();
    workerLocationLayers = new L.FeatureGroup();
    map: L.map;
    layer: L.Layer;
    initZoom = 8;
    initLatlng = [2.893666551820528,102.77709960937501];
    markerOptions = {  //required for having icon
    	icon: L.icon({
    		iconSize: [ 25, 41 ],
    		iconAnchor: [ 13, 41 ],
    		iconUrl: '/assets/map/marker-icon.png',
    		shadowUrl: '/assets/map/marker-shadow.png'
    	})
    };

    polylineOptions = false;
    circleOptions = false;
    circleMarkerOptions = false;
    rectangleOptions = false;

    polygonOptions = {
    	color: 'blue'
    };
    geofenceOptions = {
        color: 'red'
    };
    zoneOptions = {
        color: 'yellow'
    };

    worker = [];
    /* END */    

    /* Polygon Creation Options */
    //refer to leafletjs.com/reference-1.2.0.html for other options
    drawOptions = {
    	position: 'topright',

    	draw: {
    		marker: false,
    		polyline: this.polylineOptions,  //remove from toolbar
    		circle: this.circleOptions,
    		circlemarker: this.circleMarkerOptions,
    		rectangle: this.rectangleOptions,
    		polygon: {
                allowIntersection: false,
                drawError:{
                    color: 'black',
                    message: '<strong> Cannot intersect </strong>'
                },
    			shapeOptions: this.polygonOptions
    		}
    	},
        edit: {}
    }

    drawOptions_edit = {
        featureGroup:  this.editableLayers,
        edit: {        //allow for editing layers in toolbar, false to disable
            selectedPathOptions:{
                //refer to leafletjs.com/reference-1.2.0.html#path for other options
            }    
        },
        // remove: false      //allow for removing layers in toolbar, false to disable
    };

    //MODAL BOX SETTINGS
    polygonForm: FormGroup;
    polygonFormValid: boolean = false;
    polygonValue = {
        polygon_project_id: "",
        polygon_type: "",
        valid: ""
    };
    modalBox_init(project: JSON){
    	// Get the modal
    	let modal = document.getElementById('myModal');
        let polygon_project_id = document.getElementById('polygon_project_id');
        let polygon_type = document.getElementById('polygon_type');

        for(let pointer in project){
            let optionnode = document.createElement("OPTION");
            let text = document.createTextNode(project[pointer].p_id + ' - ' + project[pointer].p_data.p_name);
            optionnode.appendChild(text);
            polygon_project_id.appendChild(optionnode);
        }
    }

    modalBox_submit(value){
        let modal = document.getElementById("myModal");
        modal.style.display = "none";
        value.valid = this.polygonFormValid;
        this.polygonValue = value;
    }

    modalBox_valid(){
        this.polygonFormValid = true;
    }

    modalBox_cancel(){
        this.polygonFormValid = false; 
    }
    //END

    searchbox_Settings = {
        //refer to https://github.com/tanoy009/ng4-geoautocomplete for options
        // useGoogleGeoApi: false,
        // geoPredictionServerUrl: "http://dev.virtualearth.net/REST/v1/Locations?key=4xNsLLMZEx4xKKiZvWza~kXA5iLUpQknyl2_y_lAd6A~Ap_le5uNwpzFqoS1jnRrMNZKwqkNNRtoWhrUwGu2tgY6nD6xSLv7Kc52WfPE8uao",
        // geoLatLangServiceUrl: "http://dev.virtualearth.net/REST/v1/Locations?key=4xNsLLMZEx4xKKiZvWza~kXA5iLUpQknyl2_y_lAd6A~Ap_le5uNwpzFqoS1jnRrMNZKwqkNNRtoWhrUwGu2tgY6nD6xSLv7Kc52WfPE8uao",
        // geoLocDetailServerUrl: "http://dev.virtualearth.net/REST/v1/Locations?key=4xNsLLMZEx4xKKiZvWza~kXA5iLUpQknyl2_y_lAd6A~Ap_le5uNwpzFqoS1jnRrMNZKwqkNNRtoWhrUwGu2tgY6nD6xSLv7Kc52WfPE8uao"
    };

    searchbox_AutoCompleteCallback(selectedData: any){
        if(selectedData.response){
            let latlng = selectedData.data.geometry.location;
            this.map.setView(latlng);
            if(this.editableLayers.hasLayer(this.layer)){
                this.editableLayers.removeLayer(this.layer);
            }
            this.layer = L.circleMarker(latlng);
            this.editableLayers.addLayer(this.layer);
        }
    };

    /* Map layers */
    map_OpenStreetMap = L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {maxZoom: 18} );
    map_GoogleCNNormalMap = L.tileLayer('http://www.google.cn/maps/vt?lyrs=m@189&gl=cn&x={x}&y={y}&z={z}', {maxZoom: 18} );
    map_GoogleCNSatelliteMap = L.tileLayer('http://www.google.cn/maps/vt?lyrs=s,h@189&gl=cn&x={x}&y={y}&z={z}', {maxZoom: 18} );
    map_TianDiTuNormal = L.layerGroup([
        L.tileLayer('http://t{s}.tianditu.cn/DataServer?T=vec_w&X={x}&Y={y}&L={z}', {maxZoom: 18, subdomains: ['0', '1', '2', '3', '4', '5', '6', '7']} ),
        L.tileLayer('http://t{s}.tianditu.cn/DataServer?T=cva_w&X={x}&Y={y}&L={z}', {maxZoom: 18, subdomains: ['0', '1', '2', '3', '4', '5', '6', '7']})
        ]);  
    map_TianDiTuSatellite = L.layerGroup([
        L.tileLayer('http://t{s}.tianditu.cn/DataServer?T=img_w&X={x}&Y={y}&L={z}', {maxZoom: 18, subdomains: ['0', '1', '2', '3', '4', '5', '6', '7']} ),
        L.tileLayer('http://t{s}.tianditu.cn/DataServer?T=cia_w&X={x}&Y={y}&L={z}', {maxZoom: 18, subdomains: ['0', '1', '2', '3', '4', '5', '6', '7']})
        ]);

    /* Initial settings */
    options = {
        layers: [
        L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {maxZoom: 18} )
        ],
        zoom: this.initZoom,
        // center: L.latLng(3.05, 101.62)
    };

    /* Being able to change to view different layers */
    layersControl = {
        baseLayers: {
            'Open Street Map': this.map_OpenStreetMap,
            'Google Normal': this.map_GoogleCNNormalMap,
            'Google Satelite': this.map_GoogleCNSatelliteMap,
            'TianDiTu Normal': this.map_TianDiTuNormal,
            'TianDiTu Satellite': this.map_TianDiTuSatellite
        },
        overlays: {
            'Projects': this.projectLayers,
            // 'Workers': this.workerLayers,
            'Geofence': this.editableLayers,
            'Zone': this.zoneLayers
        }
    };

    /* Add new stuff */
    newMarker(latlng, options, popup: string): L.marker{
        let marker = L.marker(latlng, options);
        if(popup != null){
            marker.bindPopup(new L.popup().setContent(popup));
        }
        return marker;
    }

    newCircleMarker(latlng, options, popup: string): L.circleMarker{
        let circleMarker = L.circleMarker(latlng, options);
        if(popup != null){
            circleMarker.bindPopup(new L.popup().setContent(popup));
        }
        return circleMarker;
    }

    newCircle(latlng, options, popup:string): L.circleMarker{
        let circle = L.circle(latlng,options);
        if(popup != null){
            circle.bindPopup(new L.popup().setContent(popup));
        }
        console.log(circle);
        return circle;
    }

    newPolygon(latlng, options, popup: string): L.polygon{
        let polygon = L.polygon(latlng, options);
        if(popup != null){
            polygon.bindPopup(new L.popup().setContent(popup));
        }
        return polygon;
    }

    newPopup(newPopupDetails, id: string, name: string, status: string, photo: string, location: string, type: string, lastReceivedLocation: string): L.popup{
        let popup = 
        newPopupDetails("ID", id)	+
        newPopupDetails("Name", name)	+
        newPopupDetails("Status", status)	+
        newPopupDetails("Location", location)  +
        newPopupDetails("Last Received Location", lastReceivedLocation);
        if(photo != null){
            popup = `
            <div class="row form-group" style="width:300px">
                <div class="col-lg-6">
                    <img src="`+ photo +`" style="height:inherit;width:inherit">
                </div>
                <div class="col-lg-6">` +
                    popup + 
                `</div>
            </div>
            `;
        }else{
            popup = `
            <div class="row form-group" style="width:150px">
                <div class="col-lg-12">` + 
                    popup +
                `</div>
            </div>
            `;
        }
        if(type != null){
            popup += `
            <div style="text-align:center">
                <button onClick="window.location='#/`+ type +`/`+ id +`'" style=" font-family: Roboto; background-color: #4682B4; color: #ffffff; padding: 5px; border: none;"> More Details </button>
            </div>
            `;  
        }         
        return popup;
    }

    newPopupDetails(detailname: string, detail: string): string{
        if(detail != null){
            return `
            <div>
                <b>`+ detailname +`: </b>
                <p style="margin:0">`+ detail +`</p>
            </div>
            `;	
        }return "";
    }
    /* END */

    /* Get from db */
    getDataFromAPI(callApi: CallApiService, keyName: string): Observable<JSON>{
        if(keyName == null){
            return callApi.getData(`${ApiCmd.map}`);
        }else{
            return callApi.getData(`${ApiCmd.projectList}/${keyName}`);
        }
    }
    /* END */

    /* Aquiring stored location from db */
    loadFromStorage(data: JSON){
        for(let pointer in data){
            let project = data[pointer];
            this.setProject(project);
        }
    }

    setProject(project){
        if(project === null) return;
        this.projectLayers.addLayer(
            this.newMarker(
                { lat: project.p_data.p_latitude, lng: project.p_data.p_longitude }
                , this.markerOptions
                , this.newPopup(this.newPopupDetails,project.p_id, project.p_data.p_name, null, project.p_data.p_photo, project.p_data.p_location, "projects", null)
                )
            );
        for(let gpointer in project.geofence){
            let geofence = project.geofence[gpointer];
            this.editableLayers.addLayer(
                this.newPolygon(
                    geofence.geofence_latlng
                    , this.polygonOptions
                    , this.newPopup(this.newPopupDetails,project.p_id, null, null, null, null, null, null) + '<div style="display:none;">geofence_id:'+ geofence.geofence_id +'</div>'
                    ).setStyle(this.geofenceOptions)
                );
        }
        for(let zpointer in project.zone){
            let zone = project.zone[zpointer];
            this.editableLayers.addLayer(
                this.newPolygon(
                    zone.zone_latlng
                    , this.polygonOptions
                    , this.newPopup(this.newPopupDetails,project.p_id, zone.zone_id, null, null, null, null, null)
                    ).setStyle(this.zoneOptions)
                );
        }
        this.setWorker(project);
    }

    loadFromStorage_worker(data: JSON){
        for(let pointer in data){
            this.setWorker(data[pointer]);
        }
    }

    setWorker(project){
        let zoomDifference = this.map.getZoom() - this.initZoom;
        let radius = 1.5625 * Math.pow(2,zoomDifference);

        for(let wpointer in project.worker){
            let worker = project.worker[wpointer];
            if(worker.w_dev_data === false || worker.w_dev_data.length == 0){
                continue;   
            }
            let worker_latlng = {lat: worker.w_dev_data.latitude, lng:worker.w_dev_data.longitude};

            if(worker.w_id in this.worker){
                let worker_ref = this.worker[worker.w_id];
                let worker_ref_latlng = worker_ref.marker.getLatLng();
                if(worker_ref_latlng.lat == worker_latlng.lat && worker_ref_latlng.lng == worker_latlng.lng){
                    continue;
                }else{
                    this.workerLayers.removeLayer(worker_ref.marker);
                    if('circle' in worker_ref){ this.workerLocationLayers.removeLayer(worker_ref.circle); }
                }
            }
            let workerPopup=this.newPopup(this.newPopupDetails,worker.w_id, worker.w_name_photo.w_name, worker.w_status.health_status, worker.w_name_photo.w_photo, null, "employee", worker.dev_time);
            let workerMarker = this.newCircleMarker(
                worker_latlng
                , null
                , workerPopup
            );
            this.workerLayers.addLayer(workerMarker);

            let workerLocMarker = null;
            if(worker.w_latlng_valid==='V' && (worker.w_dev_data.zone==='Not in zone' || worker.w_dev_data.zone==='' || !('zone' in worker.w_dev_data))){
                workerLocMarker = this.newCircleMarker( worker_latlng, null, workerPopup);
                this.workerLocationLayers.addLayer(workerLocMarker);
            }

            if(workerLocMarker !== null){
                this.worker[worker.w_id] = { marker: workerMarker, circle: workerLocMarker };
            }else{
                this.worker[worker.w_id] = { marker: workerMarker };
            }
        }
        this.workerLocationLayers.setStyle({radius:radius});
        this.workerLocationLayers.bringToBack();
        this.workerLayers.bringToFront();
    }

    clearMap(){
        this.zoneLayers.clearLayers();
        this.workerLayers.clearLayers();
        this.workerLocationLayers.clearLayers();
        this.editableLayers.clearLayers();
        this.projectLayers.clearLayers();
    }
    /* END */

    /* Post data to db */
    addToStorage(callApi: CallApiService, data): Observable<string>{
        let polygon = document.forms["polygonForm"];
        let value = {
            p_id: polygon["polygon_project_id"].value.split(" - ")[0],
            latlng: data,
            // type: "add"
        };
        let url;
        if(polygon["polygon_type"].value == "Geofence" || polygon["polygon_type"].value == "Geofencing"){
            url = `${ApiCmd.geofencing}`;
            // value['to'] = "geofence";
        }else{
            url = `${ApiCmd.zoning}`;
            value['zone_id'] = polygon["polygon_zone_name"].value;
            value['level'] = polygon["polygon_zone_level"].value;
            // value['to'] = "zone";
        }
        callApi.postData(url+"add", JSON.stringify(value)).subscribe(
            response => {console.log('response: '+JSON.stringify(response));}
            );
        return Observable.create((observer) => observer.next(value));
    }

    removeFromStorage(callApi: CallApiService, data, project: string, type: string){
        let value = {
            p_id: project,
            data: data,
            // type: "remove"
        }
        console.log(value);
        let url;
        if(type == "Geofence" || type == "Geofencing"){
            url = `${ApiCmd.geofencing}`;
        }else{
            url = `${ApiCmd.zoning}`;
        }
        callApi.postData(url+"remove", JSON.stringify(value)).subscribe(
            response => {console.log('response: '+JSON.stringify(response));}
        );   
    }
    /* END */

    /* All map functions */
    onMapReady(map: L.map){
        let callApi = this.callApi;

        /* Removing clear all button */
        L.EditToolbar.Delete.include({
            removeAllLayers: false
        });

        /* Display or hide the edit toolbar */
        if(this.project_id === null){
            this.drawOptions.edit = false;
        }else{
            this.drawOptions.edit = this.drawOptions_edit;
        }

        /* Allow for drawing event */
        let drawControl = new L.Control.Draw(this.drawOptions);
        map.addControl(drawControl);

        /* Starting display layer */
        let projectLayers = this.projectLayers;
        let editableLayers = this.editableLayers;
        let workerLayers = this.workerLayers;
        let workerLocationLayers = this.workerLocationLayers;
        let zoneLayers = this.zoneLayers;
        map.addLayer(workerLayers);
        map.addLayer(workerLocationLayers);
        map.addLayer(projectLayers);
        map.addLayer(editableLayers);        //comment out to not display them on initial
        map.addLayer(zoneLayers);

        /* Function from typescript */
        let addToDB = this.addToStorage;
        let removeFromDB = this.removeFromStorage;
        let newPopup = this.newPopup;
        let newPopupDetails = this.newPopupDetails;

        /* Variables */
        let geofenceOptions = this.geofenceOptions;
        let zoneOptions = this.zoneOptions;
        let legend = L.control({position: 'bottomright'});

        /* Required functions to work */
        function compareLatLng(latlng1, latlng2){
            if (latlng1.length == latlng2.length){
                for(let index=0;index<latlng1.length;index++){
                    if(latlng1[index].lat != latlng2[index].lat || latlng1[index].lon != latlng2[index].lon){
                        return false;
                    }
                }return true;;
            }return false;
        }

        function updateLayer(data, layer): L.layer{
            if(data.to == "geofence" || data.to == "geofencing"){
                layer.bindPopup(new L.popup().setContent(newPopup(newPopupDetails, data.project_id, null, null, null, null, null, null)));
                layer.setStyle(geofenceOptions);
            }else{
                layer.bindPopup(new L.popup().setContent(newPopup(newPopupDetails, data.project_id, data.zone_id, null, null, null, null, null)));
                layer.setStyle(zoneOptions);
            }
            return layer;
        }

        function getColor(d) {
            return d == 'User' ? 'blue':
                   d == 'Geofence'  ? 'red' :
                   d == 'Zone'?'yellow':'black';
        }

        let initZoom = this.initZoom;
        map.on('zoomend',function(e){
            let zoomDifference = map.getZoom() - initZoom;
            let radius = 1.5625 * Math.pow(2,zoomDifference);
            workerLocationLayers.setStyle({radius: radius});
        });

        map.on(L.Draw.Event.DRAWSTART, function(e){
            let modal = document.getElementById("myModal");
            modal.style.display = "block";
        });

        map.on(L.Draw.Event.CREATED, function (e){
            //adding stuff to stored array, probably change to save into database ?
            let type = e.layerType,
            layer = e.layer;
            let data = [];
            let latlng;
            switch(type){
                case 'marker':
                    //binds popup to the layer
                    layer.bindPopup( 
                        new L.popup()
                        .setContent(`                    //you can bind html codes here!
                            <div>I'm a marker</div>
                            <a href=\"https://www.google.com\">hi</a>  /* redirect to what ever */
                            `)
                        )
                    .on(        //you can bind events here!
                        'popupopen',  //event type
                        function(e){
                            console.log("pop up opened");
                            console.log(e.target);
                        }
                        )
                    ;
                    latlng = {0: layer.getLatLng()};
                // drawStuff.push({latlng: {0: layer.getLatLng()}, type: 'marker'});
                // console.log(drawStuff[drawStuff.length-1].latlng);  // <- this is the coordinates saved
                break;

                case 'circle':
                case 'circlemarker':
                    latlng = {0: layer.getLatLng()};
                // drawStuff.push({latlng: {0: layer.getLatLng()}, radius: layer.getRadius(), type: 'circle'});
                // console.log(drawStuff[drawStuff.length-1].latlng);  // <- this is the coordinates saved
                // console.log(drawStuff[drawStuff.length-1].radius);  // <- this is the radius of the layer saved
                break;

                case 'polygon':
                case 'rectangle':
                case 'polyline':
                    latlng = layer.getLatLngs()[0];
                // drawStuff.push({latlng: layer.getLatLngs()[0], type: 'polygon'});
                // console.log(drawStuff[drawStuff.length-1].latlng); // <- this is the coordinates saved
                break;
            }
            // data.push({latlng: latlng});
            addToDB(callApi, JSON.stringify(latlng)).subscribe(
                data => {
                    console.log(data);
                    layer = updateLayer(data, layer);
                    // handle same zone name
                    editableLayers.addLayer(layer);
                }
                );
        });

        map.on(L.Draw.Event.EDITED, function (e){
            // blah blah blah do edit stuff
            console.log(e);
        });

        legend.onAdd = function (map) {

            var div = L.DomUtil.create('div', 'info legend'),
                legend = ['User','Geofence','Zone'],
                labels = [];

            // loop through our density intervals and generate a label with a colored square for each interval
            for (var i = 0; i < legend.length; i++) {
                div.innerHTML +=
                    '<i style="background:' + getColor(legend[i]) + '">&nbsp;&nbsp;&nbsp;</i> ' +
                    legend[i] + '<br>' ;
            }

            return div;
        };

        map.on(L.Draw.Event.DELETED, function (e){
            //remove from stored array, probably change to remove from database ?
            let array = e.layers._layers;
            if(array.length<1){
                return;
            }
            let data_geo = [];
            let data_zone = [];
            let text;
            let project_id;
            for(let arrayPointer in array){
                let removing = array[arrayPointer];
                let latlng;

                if('_latlng' in removing){
                    if('_radius' in removing){
                        console.log("removing a circle / circlemarker");
                    }else{
                        console.log("removing a marker");
                    }
                    latlng = {0: removing.getLatLng()};
                }else if('_latlngs' in removing){
                    if('_poly' in removing.editing){
                        console.log("removing a polygon/polyline");
                    }else{
                        console.log("removing a rectangle");
                    }
                    latlng = removing.getLatLngs()[0];
                }
                text = removing._popup._contentNode.innerText.replace(/ +/g,"").trim();
                project_id = text.substring(text.indexOf("ID:")+3).trim();
                let name;
                if((name = project_id.indexOf("Name:")) >= 0){
                    let zone_name = project_id.substring(name+5).trim();
                    project_id = project_id.substring(0, name).trim();
                    data_zone.push({latlng: latlng, zone_id: zone_name});
                }else if((name = project_id.indexOf("geofence_id:")) >= 0){
                    let geofence_id = project_id.substring(name+12).trim();
                    project_id = project_id.substring(0, name).trim();
                    data_geo.push({latlng: latlng, geofence_id: geofence_id});
                }
            }
            if(data_geo.length > 0){
                removeFromDB(callApi, JSON.stringify(data_geo), project_id, "Geofencing");
                console.log("removing geofence");
            }
            if(data_zone.length> 0){
                setTimeout(function(e){
                    removeFromDB(callApi, JSON.stringify(data_zone), project_id, "Zoning");
                console.log("removing zone");
                }, 2000);
            }
        });

        this.map = map;
        legend.addTo(this.map);
    }



    focusOnMap(event){
        let zoom=15;
        console.log(event);
        let latlng = L.latLng(event.lat, event.lng);
        if(latlng)
            this.map.setView(latlng,zoom);
        else
            alert("User location not found");
        this.showPopup(event.w_id,this.workerLayers);
    }

    //show Popup when click on a user
    showPopup(w_id,lyrs){
        let layers=lyrs.getLayers();
        for(let lyr of layers){
            console.log(lyr);
            let text = lyr.getPopup().getContent().replace(/(<\/?[^<]*>)/g,"").trim();
            let worker_id = text.substring(text.indexOf("ID:")+3).trim();
            let name;
            if((name = worker_id.indexOf("Name:")) >= 0){
                worker_id = worker_id.substring(0, name).trim();
            }
            // console.log(worker_id);
            // console.log(w_id);
            if(w_id==worker_id){
                console.log("same");
                lyr.openPopup();
            }
            
        }
    }
    /* END */

}



