import { Component, OnInit, Output,EventEmitter } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { CallApiService, ApiCmd,ApiReloadService } from "../../../shared/services";

@Component({
	selector: 'app-add-project',
	templateUrl: './add-project.component.html',
	styleUrls: ['./add-project.component.scss']
})
export class AddProjectComponent implements OnInit {
	projectForm:FormGroup;
	@Output()
	formSubmitSuccess=new EventEmitter<boolean>();
	//photo variable
	photo:any;
	constructor(private fb:FormBuilder,
             	private reloadService:ApiReloadService,
				private callApi:CallApiService) { }

	ngOnInit() {
		this.projectForm = this.fb.group({
			projectId:['',[Validators.required,Validators.maxLength(50)]],
			projectName:['',[Validators.required,Validators.maxLength(50)]],
			projectAdmin:['',[Validators.required]],
			projectStartDate:[new Date(),[Validators.required]],
			projectLocation:['',[Validators.required,Validators.maxLength(80)]],
			projectLatLng:[''],
			projectSize:[''],
			projectStories:[''],
			// projectNumberOfWorkers:[''],
			projectWorkers:new FormArray([])
		})
	}

	newFile(event){
		console.log(event);
		this.photo=event;
	}

	onSubmit(value){
		console.log(value);
		value['projectStartDate'] = (new Date(value['projectStartDate'])).toISOString();
		if(value['projectSize'] === '') value['projectSize'] = '0';
		if(value['projectStories'] === '') value['projectStories'] = '0';
		this.callApi.postWithFile(ApiCmd.newProject,value,this.photo)
		.subscribe((response)=>{
			this.formSubmitSuccess.emit(true);
			console.log(response);
			this.reloadService.setReload();
		});
	}

}



