import { Component, OnInit } from '@angular/core';
import { TabService } from "../../shared/services";
import { Subject } from 'rxjs/Rx';
import { CallApiService, ApiCmd,ApiReloadService } from "../../shared/services";
import { AuthService,Profile } from "../../shared";

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss']
})
export class ProjectsComponent implements OnInit {
	allProjects:any[];
  ongoingProjects:any[];
  pastProjects:any[];
  attachedProjects:any[] = [];
	// set current content display the first tab
	currentTab:string="ongoingProjects";
	constructor(private tabService:TabService,
              private reloadService:ApiReloadService,
              private callApi:CallApiService,
              private authService:AuthService) {
      this.currentTab=this.tabService.currentProject;
  		this.tabService.currentProjectTab$.subscribe(
  			(tab)=>{
  				//set currentTab to clicked tab from tab.services.
          // console.log("tab changes "+tab);
  				this.currentTab=tab;
			});

      //reload list when new project is added
      this.reloadService.reload$.subscribe(
        (reload)=>{
          if(reload){
            this.allProjects=null;
            this.ongoingProjects=null;
            this.pastProjects=null;
            this.attachedProjects=null;
            setTimeout(() => {this.getAllList(ApiCmd.projectList),this.getOngoingList(ApiCmd.projectListOngoing),this.getPastList(ApiCmd.projectListPast),this.getAttachedList(ApiCmd.projectListAttached)}, 2000);
          }
        }
      );


	}
  //Attached
    //get the user data from authService 
    profile:Profile;

  ngOnInit() {
    this.getAllList(ApiCmd.projectList);
    this.getOngoingList(ApiCmd.projectListOngoing);
    this.getPastList(ApiCmd.projectListPast);
    this.profile=this.authService.getUserData();
    this.attachedProjects = this.attachedProjects.concat(this.profile.project);
    for(let i=0;i<this.attachedProjects.length;i++){
      this.attachedProjects[i]['p_admin'] = this.profile.u_name;
    }
  }

  // get project list
  getAllList(url:string){
  	this.callApi.getData(url)
  		.subscribe(data=>{
  			this.allProjects=data;
    		})
  }

  //get ongoing project list
  getOngoingList(url:string){
    this.callApi.getData(url)
      .subscribe(data=>{
        this.ongoingProjects=data;
        })
  } 

  //get past project list
  getPastList(url:string){
    this.callApi.getData(url)
      .subscribe(data=>{
        this.pastProjects=data;
        })
  }

  getAttachedList(url:string){
    this.callApi.getData(url+"/"+this.profile.u_id)
      .subscribe(data=>{
        this.attachedProjects=data;
        for(let i=0;i<this.attachedProjects.length;i++){
          this.attachedProjects[i]['p_admin'] = this.profile.u_name;
        }
        });
  }

}



