import { Component, OnInit } from '@angular/core';
import { FormComponent } from '../../shared/form/form.component';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CallApiService, ApiCmd, ApiReloadService, CustomValidators } from "../../shared/services";


@Component({
	selector: 'app-admin',
	templateUrl: './admin.component.html',
	styleUrls: ['./admin.component.scss']
})
export class AdminComponent implements OnInit {
	adminForm:any[];
	group:FormGroup;


	//configuration for feedback message
	formSubmitted:boolean=false;
	indicationMessage:Array<string>=["Success","NewAdminFormsIsSubmitted"];
	constructor(private fb:FormBuilder) {
		this.group=this.fb.group({
			name: ['',[Validators.required,Validators.maxLength(50)]],
			age: ['',[Validators.required,Validators.maxLength(50)]],
			email: ['',[Validators.required,Validators.email,Validators.maxLength(50)]],
			password:['',[Validators.required,Validators,Validators.minLength(8)]],
			phone: ['',[Validators.required,Validators.minLength(8),Validators.maxLength(15)]],
			photo: ['']
		});

	}

	ngOnInit() {
		this.constructAdminForm();
	}

	formSubmit(event){
		this.formSubmitted=event;
	}

	constructAdminForm():void{
		this.adminForm=[
		{
			form_group:this.group,
			submit_url:ApiCmd.newAdmin,
			contain_image:true,
			form_control_names:[
				{
					"input_name":"Name",
					"form_control_name":"name",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"ExampleName",
					"error_message":"NameError"
				},{
					"input_name":"Age",
					"form_control_name":"age",
					"type":{type:"number",options:[],accept:""},
					"placeholder":"30",
					"error_message":"RequiredError"
				},{
					"input_name":"EmailAddress",
					"form_control_name":"email",
					"type":{type:"email",options:[],accept:""},
					"placeholder":"ExampleEmail",
					"error_message":"EmailError"
				},{
					"input_name":"Password",
					"form_control_name":"password",
					"type":{type:"password",options:[],accept:""},
					"placeholder":"",
					"error_message":"PasswordError"
				},{
					"input_name":"Contact",
					"form_control_name":"phone",
					"type":{type:"text",options:[],accept:""},
					"placeholder":"ExamplePhone",
					"error_message":"ContactError"
				},{
					"input_name":"Photo",
					"form_control_name":"photo",
					"type":{type:"file",options:[],accept:"image/*"},
					"placeholder":"",
					"errorMessage":""
				}
			]

		}
	];
	}

}



