import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
	selector: 'app-info-row',
	templateUrl: './info-row.component.html',
	styleUrls: ['./info-row.component.scss']
})
export class InfoRowComponent implements OnInit, OnChanges {
	@Input() title: any;
	@Input() value: any;
	@Input() edit: boolean;
	@Input() group:FormGroup;
	@Input() controlName:FormControl;

	constructor() { }

	ngOnChanges(){
		// console.log(this.title, this.value);
		if(this.value === '' || this.value === null || typeof this.value === 'undefined'){
			this.value = '-';
		}
		// console.log("info-row: "+this.edit);
	}

	ngOnInit() {
	}

}



