import { FormControl, Validators } from '@angular/forms';

// setup simple regex for white listed blood
const validBlood = /^(A|B|AB|O)[+-]?/;
//2017-12-05T13:15:16.000Z
const validDate = /^d{4}-d{2}-d{2}Td{2}:d{2}:d{2}\.+/
// create your class that extends the angular validator class
export class CustomValidators extends Validators {

  // create a static method for your validation
  static validateBloodType(control: FormControl) {

    // first check if the control has a value
    if (control.value && control.value.length > 0) {

      // match the control value against the regular expression
      const matches = control.value.match(validBlood);
      
      // if there are matches return an object, else return null.
      return matches && matches.length ? null : { valid: matches };
    } else {
      return null;
    }
  }

  static validateSelect(control: FormControl) {
    // console.log(control.value);
    if (control.value > 0){
      return {valid: control.value };
    }else{
        return null; 
    }
  }
}


